.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* End of Default Style */
.main-wrap{
text-align: center;
}
.wrapper{
  background-image: url("./sreerajnair.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  width: 100%;
}
.header-cont{
  display: flex;
  justify-content: space-between;
  align-items: center;
  /*background-color: #25177b6b;
  background: linear-gradient(180deg, #25177b6b,#25177b6b, transparent);*/
  background-color: #00000091;
  padding: 0px 35px;
  color:#fff;
}
.header-cont h3.About{
  color:#fff;
  text-decoration: underline;
  text-decoration-color: #fff;
}
.brand{
    color: #fff;
    text-transform: uppercase;
    font-family: fantasy;
    animation: App-logo-spin infinite 20s linear;
}
.homepage{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 700px;
  
  padding: 0px 35px;
  /*background: radial-gradient(#ffffffb0, transparent);
  width:  100%;*/
  background-color: #00000091;
  color: #fff;
}
.my-name, .my-designation{
  font-size: 70px;
  margin: 0px;
  
}
.my-name{
  color: #42ff7e;
}
.wishing{
  font-size: 50px;

}
.description{
  margin: 30px 0px;
}